$path: 'static-assets/fonts/';

@font-face {
  font-family: 'Font Awesome 5 Pro';
  font-style: normal;
  font-weight: 900;
  src: url($path + "fa-solid-900.eot");
  src: url($path + "fa-solid-900.eot?#iefix") format("embedded-opentype"), url($path + "fa-solid-900.woff2") format("woff2"), url($path + "fa-solid-900.woff") format("woff"), url($path + "fa-solid-900.ttf") format("truetype"), url($path + "fa-solid-900.svg#fontawesome") format("svg"); }

.fa,
.fas {
  font-family: 'Font Awesome 5 Pro';
  font-weight: 900; }
